.item{
  width: 280px;
}


.item img {
   width: 280px;
}

.item p{
  margin: 6px 0px;
}

.item-prices{
  display: flex;
  gap: 20px;
}

.item-prices-new{
  color: #374151;
  font-weight: 600;
  font-size: 18px;
}
.item-prices-old{
  color:#8c8c8c;
  font-size: 18px;
  font-weight: 500px;
  text-decoration: line-through;
}

.item:hover {
   transform: scale(1.05);
   transition: 0.6s;
}

@media (max-width:1280px) {
.item{
    width:220px;
    font-size: 14px;
  }

.item img {
    width: 220px;

    }
.item-prices-old{
 font-size:14px ; 
}

}

@media (max-width:1024px){
  
  .item{
    width: 170px;
    font-size: 13px;
  }

  .item img{
    width: 170px;
  }

 .item-prices-old {
  font-size: 13px;
}
  
}


@media (max-width:800px) {

  .item {
    width: 120px;
    font-size: 12px;
  }

  .item img {
    width: 120px;
  }

  .item-prices-old {
    font-size: 12px;
  }

}


@media (max-width:660px){
  .item {
      width: 160px;
  }

    .item img {
      width: 160px;
    }
}
